/**
 * 
 * 
 */
export function calculateDegreeFromSign(position) {
  const zodiacOrder = [
    '', '', '', '', '', '',
    '', '', '', '', '', ''
  ];
  
  const signIndex = zodiacOrder.indexOf(position.sign);
  if (signIndex === -1) return 0;
  
  return signIndex * 30 + position.degree + (position.minute / 60);
}


